body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.productContent{box-sizing:border-box;padding-left:40px;width:60%}.productContent .title h2{color:#232323;font-size:30px}.productContent .rating{display:flex;margin:15px 0px}.productContent .rating img{margin-left:2px}.productContent .price h3{color:#222222;font-weight:500;font-family:"Roboto",sans-serif;font-size:20px}.productContent .description{width:100%}.productContent .description p{color:#8D8D8D;font-size:14px}.productContent .quantity{display:flex;margin:20px 0px}.productContent .quantity .count{border:1px solid #E5E5E5;width:54px;height:50px;display:flex;justify-content:center;align-items:center}.productContent .quantity .count input{color:#222222;font-size:16px;width:14px;height:14px;text-align:center;outline:none;border:none}.productContent .quantity .control .controlSign{border:1px solid #E5E5E5;width:24px;height:24px;display:flex;align-items:center;justify-content:center;cursor:pointer;user-select:none}.productContent .quantity .control .controlSign p{color:#999999;font-size:18px}.productContent .quantity .control .controlSign:hover{transition:all .4s linear;background:rgba(245,245,245,0.884)}.productContent .productOptions{display:flex}.productContent .productOptions .btn_addToCart{border-radius:1px;background:#222222;display:flex;align-items:center}.productContent .productOptions .btn_addToCart .button{text-transform:uppercase;padding:20px;font-family:"Raleway",sans-serif;font-weight:bold;font-size:12px;cursor:pointer;color:#FFF}.productContent .productOptions .btn_addToCart .button:hover{transition:all .3s ease;color:#61DAFB}.productContent .productOptions .btn_addToCart .cartIcon{padding:16px;border-left:1px solid #424242;cursor:pointer;color:#FFF}.productContent .productOptions .btn_addToCart .cartIcon:hover{transition:all .3s ease;color:#61DAFB}.productContent .productOptions .like{margin-left:15px;width:52px;height:52px;border:1px solid #E5E5E5;cursor:pointer;display:flex;align-items:center;justify-content:center}.productContent .productOptions .like:hover{transition:all .3s linear;background:#408DE4}.productContent .productOptions .compare{width:52px;height:52px;border:1px solid #E5E5E5;cursor:pointer;display:flex;flex-direction:column;align-items:center;justify-content:center}.productContent .productOptions .compare:hover{transition:background .3s linear;background:#408DE4;color:#FFF}.productContent .horizontal_line{width:100%;height:1px;background-color:#EEEEEE;margin:40px 0px}.productContent .technicalContent .characteristic{color:#777777;font-size:13px;font-weight:500}.productContent .technicalContent .property{color:#222222;font-size:13px;font-weight:700;margin-right:5px}.productContent .share{display:flex;align-items:center}.productContent .share h4{color:#222222;font-family:"Roboto",sans-serif;font-size:14px}.productContent .share .shareBlocks{display:flex;align-items:center;margin:0px 10px}.productContent .share .shareBlocks .shareItem{display:flex;align-items:center;justify-content:center;width:65px;height:24px;border:1px solid #E5E5E5;margin:0px 7px;cursor:pointer}.productContent .share .shareBlocks .shareItem img{margin:0px 5px}.productContent .share .shareBlocks .shareItem p{color:#555555}.productContent .share .shareBlocks .shareItem:hover{transition:all .4s linear;border:1px solid #ffe2e2;background:#F5F5F5}@media screen and (max-device-width: 660px){.productContent .productOptions .btn_addToCart .button{font-size:10px}}
