body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.hotDeals{width:265px;user-select:none}.hotDeals .slider{width:100%}.hotDeals .slider .sliderControl{width:100%;display:flex;align-items:center;justify-content:space-around;background-color:#EEEEEE;border:1px solid #E5E5E5;padding:10px 0}.hotDeals .slider .sliderControl h4{color:#222222;text-transform:uppercase;font-size:14px}.hotDeals .slider .sliderControl .sliderBtns{display:flex}.hotDeals .slider .sliderControl .sliderBtns .btn_prev,.hotDeals .slider .sliderControl .sliderBtns .btn_next{margin:0em 0.1875em}.hotDeals .slider .sliderControl .sliderBtns .btn_prev button,.hotDeals .slider .sliderControl .sliderBtns .btn_next button{border-radius:50px;width:27px;height:27px;outline:none;border:1px solid #E6E6E6;color:#777777}.hotDeals .slider .sliderControl .sliderBtns .btn_prev button:hover,.hotDeals .slider .sliderControl .sliderBtns .btn_next button:hover{transition:all .1s linear;background-color:#85DCD6;border:1px solid #2A6577;color:#F5F5F5}.hotDeals .slider .product{text-align:center;border:1px solid #E5E5E5;cursor:pointer}.hotDeals .slider .product .product__photo{width:250px;height:245px}.hotDeals .slider .product .productInf{padding-bottom:25px}.hotDeals .slider .product .productInf .product__title{font-size:14px;font-family:"Roboto",sans-serif;color:#444444}.hotDeals .slider .product .productInf .product__price{color:#222222;font-size:13px;font-family:"Roboto",sans-serif}.hotDeals .slider .product .productInf .rating{display:flex;align-items:center;justify-content:center}.hotDeals .slider .product .productInf .rating img{margin:0px 2px}.hotDeals .slider .timer{border:1px solid #E5E5E5;background-color:#F6F6F6;display:flex;justify-content:space-between}.hotDeals .slider .timer .timer__days,.hotDeals .slider .timer .timer__hours,.hotDeals .slider .timer .timer__minutes{text-align:center;border-left:1px solid #E5E5E5;padding:0px 30px}.hotDeals .slider .timer .timer__days h6,.hotDeals .slider .timer .timer__hours h6,.hotDeals .slider .timer .timer__minutes h6{font-family:"Roboto",sans-serif;color:black;margin-block-start:2em;margin-block-end:0}.hotDeals .slider .timer .timer__days p,.hotDeals .slider .timer .timer__hours p,.hotDeals .slider .timer .timer__minutes p{color:#777777;font-size:13px}.hotDeals .slider .timer .timer__days{border-left:none}.productWrapper{text-decoration:none}@media screen and (max-device-width: 1400px){.hotDeals{display:none}}
