body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container,.billingDetails{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.displayFlexContainer{display:flex}.starSign{color:#FC4242}.billingDetails .title h3{text-transform:uppercase;color:#222222;font-size:18px;font-family:"Raleway",sans-serif}.billingDetails .form{display:flex;justify-content:space-between}.billingDetails .form .mainFormContent{width:40%}.billingDetails .form .mainFormContent .formItem{width:100%}.billingDetails .form .mainFormContent .formItem p{color:#666666;font-size:14px}.billingDetails .form .mainFormContent .formItem input{border:1px solid #E8E8E8;border-radius:1px;margin:10px 0px}.billingDetails .form .mainFormContent .formItem .shortInput{width:100%;height:45px}.billingDetails .form .mainFormContent .formItem .wideInput{width:100%;height:45px}.billingDetails .form .mainFormContent .country{width:100%}.billingDetails .form .mainFormContent .country select{width:100%;height:45px;border:1px solid #E8E8E8;color:#777777;padding:0 15px}.billingDetails .form .orderNotes{width:45%}.billingDetails .form .orderNotes p{color:#666666;font-size:14px}.billingDetails .form .orderNotes textarea{width:100%;height:80px;resize:none}@media screen and (max-device-width: 776px){.billingDetails .form{display:flex;flex-direction:column;align-items:center}.billingDetails .form .mainFormContent{width:90%}.billingDetails .form .orderNotes{width:90%}}
