body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container,.cartList{width:75%;margin:0 auto}img{user-select:none}h1,h2,h3,h4{font-family:"Raleway",sans-serif}p,li{font-family:"Roboto",sans-serif}button,a{cursor:pointer}ul{list-style:none;margin-block-start:0;margin-block-end:0;padding-inline-start:0}.dateWrapper .date{width:40px;height:45px;border-radius:1px;background-color:#408DE4;color:#FFF;text-transform:uppercase;font-family:"Roboto",sans-serif;user-select:none;display:flex;flex-direction:column;justify-content:center;align-items:center;font-weight:bold}.dateWrapper .date .day{font-size:18px;margin-block-start:0;margin-block-end:0}.dateWrapper .date .month{font-size:11px;margin-block-end:0;margin-block-start:0}.cartList{padding:30px 0px}.cartList .title h3{color:#232323;text-transform:uppercase;font-size:22px;font-weight:900}.cartList .productContainer .controls{box-sizing:border-box;padding:0px 30px;width:100%;display:flex;align-items:center;background:#FAFAFA;border:1px solid #bcbcbc;height:60px;justify-content:space-around}.cartList .productContainer .controls>div{width:20%}.cartList .productContainer .controls h5{color:#222;text-transform:uppercase}@media screen and (max-device-width: 1030px){.cartList{width:100%}}@media screen and (max-device-width: 800px){.cartList{width:125%;position:relative;transform:translate(-10%)}}@media screen and (max-device-width: 460px){.cartList .title h3{color:#232323;text-transform:uppercase;font-size:15px;font-weight:900}.cartList .productContainer .controls{box-sizing:border-box;padding:0px 30px;width:100%;display:flex;align-items:center;background:#FAFAFA;border:1px solid #bcbcbc;height:60px;justify-content:space-around}.cartList .productContainer .controls>div{width:20%}.cartList .productContainer .controls h5{font-size:8px;color:#222;text-transform:uppercase}}
